 @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-family: "Inter", sans-serif;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #F5F5F5;
}

ul,
ol {
  list-style-type: none;
}

